import React from 'react'
import { Link } from 'react-router-dom'

const Coins = () => {
    return (
        <section
            className="pt-8 pb-5 position-relative overflow-hidden"
            data-aos="fade-up"
        >
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col text-center">
                        <h1 className="display-5 fw-semibold">Supported Coins and XRC-20 Tokens</h1>
                        <p className="text-muted mx-auto">
                            Fastest way to buy or sell{" "}
                            <span className="text-dark fw-medium">popular</span> crypto coins.
                        </p>
                    </div>
                </div>
                <div className="row mt-3">
                    <div className="col-lg-3 col-md-6">
                        <div className="d-flex align-items-center py-lg-2 my-4">
                            <img
                                src="./assets/images/icons/coins/xdc.svg"
                                className="icon me-3"
                                alt=""
                            />
                            <div className="flex-grow-1">
                                <h4 className="my-0 fw-medium">XDC</h4>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-6">
                        <div className="d-flex align-items-center py-lg-2 my-4">
                            <img
                                src="./assets/images/icons/coins/pli.svg"
                                className="icon me-3"
                                alt=""
                            />
                            <div className="flex-grow-1">
                                <h4 className="my-0 fw-medium">PLI</h4>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-6">
                        <div className="d-flex align-items-center py-lg-2 my-4">
                            <img
                                src="./assets/images/icons/coins/comtech.svg"
                                className="icon me-3"
                                alt=""
                            />
                            <div className="flex-grow-1">
                                <h4 className="my-0 fw-medium">Comtech</h4>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-6">
                        <div className="d-flex align-items-center py-lg-2 my-4">
                            <img
                                src="./assets/images/icons/coins/lawblocks.svg"
                                className="icon me-3"
                                alt=""
                            />
                            <div className="flex-grow-1">
                                <h4 className="my-0 fw-medium">LawBlocks</h4>
                            </div>
                        </div>
                    </div>


                </div>
                <div className="row mt-4">
                    <div className="col-lg-12 mt-4 mt-lg-2 text-center">
                        <Link to="#" className="btn btn-primary">
                            View complete list{" "}
                            <i className="ms-2 icon icon-xs" data-feather="arrow-right" />
                        </Link>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Coins
